import React from 'react';
import styles from './Users.module.css';
import Avatar from './../Shared/Avatar.jsx';
import Preloader from './../Shared/Preloader.jsx';
import {NavLink} from 'react-router-dom';

const Users = (props) => {
    let pagesCount = Math.ceil(props.usersCount / props.usersPerPage);

    const getPagination = () => {
      let p = [];
      for (let i = 1; i <= pagesCount; i++) {
        p.push(i);
      }
      if (pagesCount > 9) {
        let spanCurrentPage = '';
        if (props.currentPage > 1 && props.currentPage < pagesCount) {
          spanCurrentPage = props.currentPage;
        }
        return <span>
          <span onClick={() => {props.onPageChange(1)}} className={props.currentPage === 1 ? styles.activePage : styles.normalPage}>{1}</span>
          <span> ..<span className={styles.activePage}>{spanCurrentPage}</span>.. </span>
          <span onClick={() => {props.onPageChange(p.length)}} className={props.currentPage === p.length ? styles.activePage : styles.normalPage}>{p.length}</span>
        </span>
      } else {
        return (p.map(item => {
              return <span onClick={() => {props.onPageChange(item)}} className={props.currentPage === item ? styles.activePage : styles.normalPage}>{item}</span>
            })
          )
      }
    }

    let setNextPage = () => {
      let nextPageNumber = props.currentPage + 1;
      if (nextPageNumber < pagesCount) {
        props.onPageChange(nextPageNumber);
      }
    }

    let setPreviousPage = () => {
      let previousPageNumber = props.currentPage - 1;
      if (previousPageNumber > 0) {
        props.onPageChange(previousPageNumber);
      }
    }

    let users = props.users.map(user => {
        let userURL = 'profile/' + user.id;
        return (
            <div key={user.id} className={styles.user}>
              <div className={styles.block__left}>
                <NavLink to={userURL}>
                  <Avatar src={user.photos.small} name={user.name} />
                </NavLink>
                <span className={styles.name}>{user.name}</span>
                {user.followed ?
                  <button  onClick={() => {props.onUnfollow(user.id)}} disabled={props.usersInFollowingProgress.some(user2 => user2 === user.id ? true : false )} className={styles.follow}>unfollow</button> :
                  <button  onClick={() => {props.onFollow(user.id)}} disabled={props.usersInFollowingProgress.some(user2 => user2 === user.id ? true : false )} className={styles.follow}>follow</button>
                }
              </div>
              <div className={styles.block__right}>
                <span className={styles.type}>I was born as </span>
                <span className={styles.location}>I currently living at
                  <span className={styles.city}>user.location.city </span>
                  <span className={styles.country}>user.location.country</span>
                </span>
                <div className={styles.status}>Status: {user.status}</div>
              </div>
            </div>
        )}
   )
   return (
     <div className={styles.userWrapper}>
        <span>
          <span onClick={setPreviousPage}>&larr; </span>
            {getPagination()}
          <span onClick={setNextPage}> &rarr;</span>
        </span>
        {props.isFetching ? <Preloader /> : users}
    </div>
   )
}

export default Users;
